// Dependencies
import { createSlice } from '@reduxjs/toolkit';

// Types
import { IOrder } from '../types/IGoods';

const basketSlice = createSlice({
  name: 'basket',
  initialState: {
    order: [] as IOrder[],
    isBasketOpen: false
  },
  reducers: {
    showTheBasket(state) {
      state.isBasketOpen = !state.isBasketOpen
    },
    addToBasket(state, action) {
      // ÐÐ»Ñ Ð¾ÑÐ¾Ð±ÑÐ°Ð¶ÐµÐ½Ð¸Ñ ÐºÐ¾Ð»Ð¸ÑÐµÑÑÐ²Ð° ÑÐ½Ð¸ÐºÐ°Ð»ÑÐ½ÑÑ Ð¿ÑÐµÐ´Ð¼ÐµÑÐ¾Ð² Ð² ÐºÐ¾ÑÐ·Ð¸Ð½Ðµ
      const itemIndex = state.order.findIndex((orderItem) => orderItem.id === action.payload.id);
      let newOrder = null;

      // Ð£ÑÐ»Ð¾Ð²Ð¸Ðµ Ð¿ÐµÑÐ²Ð¾Ð³Ð¾ ÑÐ»ÐµÐ¼ÐµÐ½ÑÐ°
      if (itemIndex < 0) { // ÐÑÐ»Ð¸ ÑÐ½Ð¸ÐºÐ°Ð»ÑÐ½ÑÐ¹ Ð¿ÑÐµÐ´Ð¼ÐµÑ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÑÑÑ Ð²Ð¿ÐµÑÐ²ÑÐµ
        const newItem = { ...action.payload, quantity: 1 };
        newOrder = [...state.order, newItem];
      } else { // ÐÑÐ»Ð¸ ÑÐ»ÐµÐ¼ÐµÐ½Ñ ÑÐ¶Ðµ ÐµÑÑÑ Ð² ÐºÐ¾ÑÐ·Ð¸Ð½Ðµ
        newOrder = state.order.map((orderItem, index) => {
          if (index === itemIndex) {
            return { ...orderItem, quantity: orderItem.quantity + 1 };
          } else {
            return orderItem;
          }
        });
      }

      return {
        ...state,
        order: newOrder,
      };
    },

    removeFromBasket(state, action) {
      state.order = state.order.filter((item) => item.id !== action.payload);
    },

    incQuantity(state, action) {
      state.order = state.order.map((el) => {
        if (el.id === action.payload) {
          const newQuantity = el.quantity + 1;
          return { ...el, quantity: newQuantity };
        } else {
          return el;
        }
      });
    },

    decQuantity(state, action) {
      state.order = state.order.map((el) => {
        if (el.id === action.payload) {
          const newQuantity = el.quantity - 1;
          return {
            ...el,
            quantity: newQuantity >= 0 ? newQuantity : 0,
          };
        } else {
          return el;
        }
      });
    },
  },
});

export const { addToBasket, removeFromBasket, incQuantity, decQuantity, showTheBasket } = basketSlice.actions;

export default basketSlice.reducer;
